.memory-box {
  display: inline-block;
  border: 1px solid black;
  margin: 10px;
  text-align: center;
  border-radius: 10px;
}

body {
  background-color: yellow;
}

.memory-box:hover {
  border: 4px solid yellowgreen;
  cursor: pointer;
}

.memory-box-image {
  vertical-align: top;
}

.memory-box-image img {
  padding: 5px;
}

.memory-box-title h3 {
  font-style: italic;
  color: red;
}

.loaderDiv svg {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 200px;
}

.GameScore {
  float: right;
  margin-top: -90px;
}

.GameScore h4 {
  margin-top: 4px;
  margin-bottom: 4px;
  margin-right: 30px;
}

.container {
  width: 95%;
  margin-left: auto;
  margin-right: auto;
}